// POSITIONS
.relative    { position: relative; }
.absolute    { position: absolute; }
.static      { position: static; }
.fixed       { position: fixed; }

// 0 positions
.top-0 	     { top: 0;}
.bottom-0    { bottom: 0;}
.right-0     { right: 0; }
.left-0      { left: 0 }

// 50% positions
.top-50      { top: 50% }
.bottom-50   { bottom: 50% }
.right-50    { right: 50% }
.left-50     { left: 50% }

//WIDTH AND HEIGHTS
.full-width  { width: 100% }
.full-height { height: 100% }
.width-50	 { width: 50% }
.width-0 	 { width: 0 }
.width-auto  { width: auto }
.height-auto { height: auto }
